.title {
  .title__wrapper {}

  .title__slider {}

  .title__slide {
    height: 465px;
    position: relative;
    padding: 98px 113px 98px 120px;
    overflow: hidden;

    border-radius: 0 50px 0 50px;
    clip-path: polygon(4% 0, 100% 0, 100% 90%, 96% 100%, 0 100%, 0 8%);
  }

  .title__slide-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left;
  }

  .title__slide-body {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
  }

  .title__slide-img {
    &.--desc {}

    &.--mob {
      display: none;
    }
  }
}


