@media screen and (max-width: 1350px) {
  .xs-hidden {
    display: none;
  }

  .title .title__slide-arrow {
    display: none;
  }

  .title .title__slide-img {
    margin: 0 auto;

    &.--desc {
      display: none;
    }

    &.--mob {
      display: block;
    }
  }

  .title .title__slide {
    height: auto;
    padding: 50px 48px 142px;
    clip-path: polygon(4% 0, 100% 0, 100% 90%, 90% 100%, 0 100%, 0 8%);
  }

  .top-new .top-new__body {
    padding: 0;
    flex-direction: column-reverse;
    border-radius: 20px;
  }

  .top-new .top-new__right {
    max-width: 100%;
    margin: 0;
    width: 100%;
  }

  .top-new .top-new__left {
    padding: 10px 20px 0;
  }

  .top-new .top-new__header {
    margin: 10px 0;
  }

  .competencies .competencies__item {
    height: 290px;
    justify-content: center;
    font-size: 24px;
    font-weight: 500;
    padding: 30px;
  }

  .news .news__items {
    grid-template-columns: 1fr;
  }

  .subscribe .subscribe__wrapper {
    padding: 60px 49px;
    clip-path: polygon(10% 0, 100% 0, 100% 90%, 90% 100%, 0 100%, 0 10%);
  }

  .subscribe .subscribe__body {
    display: flex;
    justify-content: start;
    flex-direction: column;
    align-items: center;
  }

  .subscribe .subscribe__header {
    font-size: 32px;
    line-height: 1.1;
  }
}