.header {
    padding-top: 9px;
    padding-bottom: 50px;
    .header__wrapper {
        display: flex;
        justify-content: space-between;
    }
    .header__logo {
        margin-right: 50px;
    }
    .header__left {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__menu {
    }
    .header__menu-inner {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__menu-item {
        font-family: 'Euclid Square';
        font-weight: 500;

        &:not(:last-child) {
            margin-right: 50px;
        }
        &.--mob {
            display: none;
        }   
    }
    .header__search-btn {
    }
    .header__burger {
        margin-left: 27px;
        display: none;
        cursor: pointer;
    }
    .header__burger-normal {
    }
    .header__burger-close {
        display: none;
    }
    .header__lang {
        margin-left: 58px;
        font-weight: 500;
    }
}

@media screen and (max-width: 1350px) {
    .header .header__lang,
    .header .header__menu {
        display: none;
    }
    .header .header__burger {
        display: block;
    }
    .header__search-btn {
        width: 19px;
        height: 21px;
    }
    .header__search-btn svg {
        width: 19px;
        height: 21px;
    }
}