@mixin page-h2  {
  font-family: 'Euclid Square';
  font-weight: 400;
  font-size: 40px;
  line-height: 100%;
  letter-spacing: 0%;

  @media screen and (max-width: 1350px) {
    font-size: 20px;
    font-weight: 700;
  }
}

@mixin page-h4  {
  font-family: 'Euclid Square';
  font-weight: 500;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 0%;
}
@mixin page-h5  {
  font-family: 'Inter';
  font-weight: 600;
  font-size: 24px;
  line-height: 100%;
  letter-spacing: 0%;
}

@mixin page-section-h2  {
  font-family: 'Euclid Square';
  font-weight: 400;
  font-size: 96px;
  line-height: 100%;
  letter-spacing: 0%;
  color: #0069d2;
  text-align: center;

  @media screen and (max-width: 1350px) {
    font-size: 40px;
  }
}

@mixin page-text-48 {
  font-family: 'Euclid Square';
  font-weight: 400;
  font-size: 48px;
  line-height: 120%;
  letter-spacing: 0%;
}
@mixin page-text-32 {
  font-family: 'Euclid Square';
  font-weight: 400;
  font-size: 32px;
  line-height: 120%;
  letter-spacing: 0%;
}
@mixin page-text-24 {
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
  letter-spacing: 0%;
}
@mixin page-text-20 {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0%;
}
@mixin page-text-16 {
  font-family: 'Inter';
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0%;
}

@mixin page-btn {
  font-family: 'Euclid Square';
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  letter-spacing: 0%;
  padding: 14px;
  border: 2px solid #0069D2;
  border-radius: 25px;
  color: #0069d2;

  max-width: 420px;
  width: 100%;
  text-align: center;
  background: none;


  &.--white {
    border-color: #fff;
    color: #fff;
  }

   @media screen and (max-width: 1350px) {
    font-size: 24px;
  }
}