*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1339px;
  margin: 0 auto;
}
@media screen and (max-width: 1350px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  padding-top: 9px;
  padding-bottom: 50px;
}
.header .header__wrapper {
  display: flex;
  justify-content: space-between;
}
.header .header__logo {
  margin-right: 50px;
}
.header .header__left {
  display: flex;
  justify-content: start;
  align-items: center;
}
.header .header__menu-inner {
  display: flex;
  justify-content: start;
  align-items: center;
}
.header .header__menu-item {
  font-family: "Euclid Square";
  font-weight: 500;
}
.header .header__menu-item:not(:last-child) {
  margin-right: 50px;
}
.header .header__menu-item.--mob {
  display: none;
}
.header .header__burger {
  margin-left: 27px;
  display: none;
  cursor: pointer;
}
.header .header__burger-close {
  display: none;
}
.header .header__lang {
  margin-left: 58px;
  font-weight: 500;
}

@media screen and (max-width: 1350px) {
  .header .header__lang,
.header .header__menu {
    display: none;
  }
  .header .header__burger {
    display: block;
  }
  .header__search-btn {
    width: 19px;
    height: 21px;
  }
  .header__search-btn svg {
    width: 19px;
    height: 21px;
  }
}
.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer a {
  color: inherit;
}
.footer .footer__wrapper {
  background: #0A1E3C;
  color: #fff;
  padding: 49px 106px 100px;
  border-radius: 0 75px 0 75px;
  clip-path: polygon(5% 0, 100% 0, 100% 95%, 95% 100%, 0 100%, 0 5%);
}
.footer .footer__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 28px;
}
.footer .footer__main {
  padding: 45px 0;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  display: grid;
  grid-template-columns: 1fr 1.3fr 1fr 1fr;
  gap: 88px;
}
.footer .footer__col-header {
  font-weight: 600;
  margin-bottom: 15px;
}
.footer .footer__col-text {
  display: block;
}
.footer .footer__col-text:not(:last-child) {
  margin-bottom: 10px;
}
.footer .footer__col-text a {
  text-decoration: underline;
}
.footer .footer__socials {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 15px;
  margin: 20px 0;
}
.footer .footer__partners {
  padding: 32px 0 50px;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #fff;
}
.footer .footer__list {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-top: 6px;
}
.footer .footer__item {
  display: flex;
  justify-content: start;
  align-items: start;
}
.footer .footer__item img {
  width: 24px;
  height: 24px;
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
}
.footer .footer__item span {
  margin-left: 11px;
  font-size: 14px;
}
.footer .footer__all {
  display: block;
  margin: 0 auto;
  font-weight: 500;
  text-decoration: underline;
  margin-top: 40px;
}
.footer .footer__bottom {
  padding-top: 51px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer .footer__bottom-item {
  font-size: 12px;
}

@media screen and (max-width: 1350px) {
  .footer .footer__wrapper {
    padding: 30px 48px;
    clip-path: polygon(50px 0, 100% 0, 100% 96%, 90% 100%, 0 100%, 0 50px);
  }
  .footer .footer__main {
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 30px 0;
  }
  .footer .footer__item span {
    display: none;
  }
  .footer .footer__item img {
    width: 40px;
    height: 40px;
  }
  .footer .footer__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px;
  }
  .footer .footer__bottom {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
  .footer .footer__bottom-item:not(:last-child) {
    margin-bottom: 10px;
  }
  .footer .footer__bottom {
    padding-top: 33px;
  }
  .footer .footer__partners {
    padding: 30px 0;
  }
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
